@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

*{
    margin: 0px;
    padding: 0px;    
    box-sizing: border-box;    
    
}

html{
    overflow-x: hidden;
}

:root{
    --typeHeaderPrimary : "Montserrat";
    --Red: red;
    --Blue: gainsboro;
    --white: gray ;
    --gainsboro : gainsboro;
}

/* HEADER */
.navbar__mega__menu__container{
    width: 100%;
    margin-bottom:5px;

}


.navbar__mega__menu{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    overflow: auto;
    white-space: nowrap;
    box-shadow: 0 5px 22px 0 rgba( 31, 38, 135, 0.37 );

}
.navbar__mega__menu::-webkit-scrollbar{
    display: none;
}

.navbar__mega__menu .MuiButton-root:hover {
    background-color: transparent;
}

.navbar__mega__menu .MuiButton-root:hover li {
  color: blue;
}

.navbar__mega__menu  span.MuiButton-label {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
img.beauty__img{
    max-width: 50px;
    height: 90px;
    padding-bottom: 1em;

}
.navbar__mega__img__container{
    height: 60px;
}
.navbar__mega__menu img {
    max-width: fit-content;
    height: 100%;
}








.mega-menu{
    display: flex;
    width: 100%;
    padding: 1em 0;
    align-items: center;
    justify-content: center;
    background-color: whitesmoke;
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );


}
.mega-menu__ul{
    display: flex;
    justify-content: center;
    margin: 0;
}

.mega-menu__ul > li {
    padding: .5em 1em ;
    font-weight: bold;
}

.mega-menu__ul  li:hover > .mega-sub__container {
    display: block;
} 

.mega-menu__ul > li:hover{
    background-color: #000;
    color: #fff;

   
}

.mega-sub__container{
    position: absolute;
    margin-top:1em;
    width: 100%;
    left: 0;
    display: none;
    background-color: whitesmoke;
    z-index: 99999999;
    padding: 3em 10em;

}
.mega-sub .MuiButton-root{
    width: 100%;
}
.mega-sub{
    display: grid;
    grid-template-columns: repeat(4 , 1fr);
    align-items: center;
    justify-content: center;
}

.mega-sub a:hover{
    text-decoration: underline;
    color: black;
}


/* HEADER END */



.header__{
    display: grid;
    grid-template-columns: 15% 50% 30%;
    gap: 10px;
    width: 100%;
    align-items: center;
}

.header__item{
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header__item li{
  font-size: 1.1em;
  margin: 1em;
 

}

.header__item li:hover{
    font-weight: bold;
  }

.header__item__searchbar{
    border: 1px solid gray;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 0px 10px;
    background-color: white;
}

.header__item__searchbar > input {
    width: 100%;
    height: 35px;
    border: none;
}

.header__item__searchbar > input::placeholder{
    padding-left: 1em;
}

.header__item__searchbar > input:focus{
    outline: none;
}

.sidebar_categories button li:hover{
    background-color: #3f51b5;
}

/* //////////// */
.storepage{
    background-color: whitesmoke;
    margin-top: 5px;
}
.storepage  > h4 {
    font-family: var(--typeHeaderPrimary);
    text-transform: uppercase;
    font-weight: bold;
    padding: 3em;
    margin: 1em;
    font-size: 18px;
    background-color: white;
}

.storepage__sidebar{
   background-color: white;
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 1em 1.5em;
    border-radius: 10px;
  
}


.storepage__sidebar__dropdown__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    
}

.storepage__sidebar__dropdown__container{
    position: sticky;
    top: 10px;
    display: flex;
    flex-direction: column;
}
.storepage__grid{
    padding: 0em 1em 1em 1em;
    display: grid;
    grid-template-columns: 24.5% 75%;
    gap: 5px;
}

.storepage__products{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 3px;
}

 .dropbtn {
    background-color: transparent;
    color: black;
    padding: 10px 16px;
    font-size: 14px;
    border: none;
    cursor: pointer;
    text-transform: uppercase;
    font-family: var(--typeHeaderPrimary);
    font-weight: bold;
  }
  
 
  
  /* The container <div> - needed to position the dropdown content */
  .storepage__sidebar__dropdown {
    position: relative;
    display: inline-block;
  }
  
  /* Dropdown Content (Hidden by Default) */
 .dropdown-content {
    display: none;
    padding: 0em 2em 2em 2em 
  }
  
  
  /* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */
 .show {display:block;}


